import React, { useState, useEffect } from "react";
import style from "./Body.module.css";
import Item from "./Items/Item";
import { useInView } from "react-intersection-observer";
import { useGetUserAnnouncementArrayQuery } from "../../../../../../services/qvipleAPI";
import QvipleLoading from "../../../../../../Loader/QvipleLoading";
import EmptyInfo from "../../../../../../Loader/EmptyInfo";
import { useSelector } from "react-redux";

function Body() {
  const [announcementList, setAnnouncementList] = useState([]);
  const [ref, inView] = useInView();
  const [state, setState] = useState(true);
  const [page, setPage] = useState(1);
  const ids = useSelector((state) => state.idChange);
  const [showingDataLoading, setShowingDataLoading] = useState(true);
  const { data, refetch } = useGetUserAnnouncementArrayQuery(
    {
      id: ids?.id,
      page: page,
      limit: 10,
    },
    { skip: !ids?.id }
  );

  useEffect(() => {
    if (ids?.id) {
      setShowingDataLoading(true);
      refetch();
    }
  }, [ids?.id, page, refetch]);
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);

  useEffect(() => {
    if (data?.announcements) {
      const uniqueDublicate = [...announcementList, ...data?.announcements];
      const uniqueRefind = [
        ...new Set(uniqueDublicate.map(JSON.stringify)),
      ].map(JSON.parse);
      setAnnouncementList(uniqueRefind);
      setShowingDataLoading(false);
    } else if (data?.announcements?.length === 0) setShowingDataLoading(false);
    else {
    }
    //for next api is call or not dashboard
    if (data?.announcements.length === 10) setState(true);
    else setState(false);
  }, [data?.announcements]);

  return (
    <div className={style.body}>
      {announcementList.map((at, index) =>
        announcementList?.length === index + 1 ? (
          <div key={index} ref={ref}>
            <Item
              data={at ? at : ""}
              time={at?.createdAt}
              date={at?.createdAt}
              text={at?.insAnnDescription}
            />
          </div>
        ) : (
          <Item
            data={at ? at : ""}
            time={at?.createdAt}
            date={at?.createdAt}
            text={at?.insAnnDescription}
          />
        )
      )}
      {showingDataLoading && <QvipleLoading />}
      {!showingDataLoading && !data.announcements?.length && (
        <EmptyInfo
          customStyleContainer={{
            marginTop: "5rem",
            height: "inherit",
          }}
          title={"No announcement created yet."}
        />
      )}
    </div>
  );
}

export default Body;
